document.addEventListener('DOMContentLoaded', function() {
    trabajos();
})

function trabajos() {
    const container = document.querySelector('.trabajos-container');
    container.appendChild(trabajoCard);

    const trabajoCard = document.createElement('DIV');
    trabajoCard.classList.add('trabajo-card');


    for(let i = 6; i >= 1; i--) {
        const trabajoCard__img = document.createElement('IMG');
        trabajoCard__img.src = `build/img/pagina-normal/${i}.webp`;
        trabajoCard__img.setAttribute("alt", "Página");

        const img_container = document.createElement('DIV');
        img_container.appendChild(trabajoCard__img);
        img_container.classList.add('trabajo-card__img-container');

        trabajoCard.appendChild(img_container);
    }

    const info_container = document.createElement('DIV');
    info_container.classList.add('info-container');
    info_container.appendChild(title);
    info_container.appendChild(info);
    trabajoCard.appendChild(info_container);

    const title = document.createElement('H3');
    title.classList.add('trabajo-card__title');

    const info = document.createElement('P');
    info.classList.add('trabajo-card__info');



};
console.log(trabajos()); 